
import React, {useState} from 'react';
import { getAuth, isSignInWithEmailLink, signInWithEmailLink } from "./firebase";


const EmailConfirm = () => {

    let [email, setEmail] = useState("");
    const [completed, setCompleted] = useState(false);

    const confirmEmail = () => {

        const auth = getAuth();
        if (isSignInWithEmailLink(auth, window.location.href)) {

            if (!email) {
                // User opened the link on a different device. To prevent session fixation
                // attacks, ask the user to provide the associated email again. For example:
                email = window.prompt('Please provide your email for confirmation');
            }

            // The client SDK will parse the code from the link for you.
            signInWithEmailLink(auth, email, window.location.href)
                .then( async (result) => {
                    const idToken = await result.user.getIdToken();

                    console.log('signed in', idToken)

                    const url = new URL(window.location.href);
                    const code = url.searchParams.get('code');

                    console.log('code', code)

                    fetch('https://cabanalabs.com/auth/user/confirm/email', {
                        method: 'POST',
                        headers: { 'Accept': 'application/json', 'Content-Type': 'application/json' },
                        body: JSON.stringify({email, code, idToken})
                    }).then((res) => {
                        console.log('sent confirm', res.ok, res.status)
                        setCompleted(true)
                    })
                    .catch((error) => {
                        console.error(error)
                    });

                })
                .catch((error) => {
                    console.error(error)
                    // Some error occurred, you can inspect the code: error.code
                    // Common errors could be invalid email and invalid or expired OTPs.
                });


        }
    }


    return (
        <div style={{ marginTop: 250 }}>
            <center>
                {!completed && (
                  <>
                    <div>Confirm your email:</div>
                    <input value={email} onChange={(e) => {
                        setEmail(e.target.value) }}
                           placeholder="Confirm email" />
                    <br /><br />
                    <button onClick={confirmEmail}>Confirm</button>
                  </>
                )}
                {completed && (<div>Completed</div>)}
            </center>
        </div>
    );
}

export default EmailConfirm;