import VerificationInput from 'react-verification-input';

import styles from './Components.module.css';

export const PhoneNumberVerification = ({ value, onChange }) => {
  return (
    <div className={styles.verificationWrapper}>
      <VerificationInput
        value={value}
        classNames={{
          input: styles.vi,
          container: styles.verificationContainer,
          character: styles.singleCharacter,
          characterSelected: styles.singleCharacterSelected,
        }}
        inputProps={{ style: { border: 'none' } }}
        placeholder={''}
        validChars='0-9'
        autoFocus={true}
        length={6}
        onChange={value => onChange(value)}
      />
    </div>
  );
};
