import React from 'react';
import Login from './login';
import EmailConfirm from './emailConfirm';
import Phone from './pages/phone';

import './assets/satoshi.css';

function App() {
  const url = new URL(window.location.href);
  console.log('App', url)
  const isPhoneAuth = !!url.searchParams.get('pat');
  const isEmailConfirm = !!url.searchParams.get('emailConfirm');
  console.log('App', isPhoneAuth, isEmailConfirm)
  return isEmailConfirm ? (
    <EmailConfirm />
  ) : isPhoneAuth ? (
    <Phone />
  ) : (
    <div />
  );
}

export default App;
