import React, { useEffect, useState } from 'react';
import { getAuth, signInWithPhoneNumber, RecaptchaVerifier } from '../firebase';

import { Button } from '../components/Button';
import { Input } from '../components/Input';
import { PhoneNumberVerification } from '../components/PhoneNumberVerification';

import styles from './Pages.module.css';

export const Phone = () => {
  // Inputs
  const [number, setNumber] = useState('');
  const [otp, setOtp] = useState('');
  const [isToVerify, setIsToVerify] = useState(false);
  const [allowSignIn, setAllowSignIn] = useState(false);
  const [final, setfinal] = useState('');
  const [captchaIndex, setCaptchaIndex] = useState(0);

  useEffect(() => {
    const auth = getAuth();
    window.recaptchaVerifier = new RecaptchaVerifier(
      'recaptcha-button',
      {
        size: 'invisible',
        callback: response => {
          // reCAPTCHA solved, allow signInWithPhoneNumber.
          //onSignInSubmit();
          setAllowSignIn(true);
        },
      },
      auth
    );
    // cleanup this component
    return () => {
      window.recaptchaVerifier.clear();
    };
  }, [captchaIndex]);

  const reloadCaptcha = () => {
    setCaptchaIndex(captchaIndex + 1);
  };

  // Sent OTP
  const sendPhoneNumberOTP = () => {
    //Format spec:
    //Please enter the phone number in a format that can be parsed into E.164 format. E.164 phone numbers are written in the format [+][country code][subscriber number including area code]
    if (number === '' || number.length < 10) return;

    const appVerifier = window.recaptchaVerifier;

    const auth = getAuth();

    signInWithPhoneNumber(auth, number, appVerifier)
      .then(result => {
        setfinal(result);
        //alert('code sent');
        setIsToVerify(true);
      })
      .catch(err => {
        alert(err);
        //NOTE: this is needed to reset the recaptcha for another attempt
        window.location.reload();
      });
  };

  // Validate OTP
  const ValidateOtp = () => {
    if (otp === null || final === null) return;
    final
      .confirm(otp)
      .then(async result => {
        const url = new URL(window.location.href);
        const code = url.searchParams.get('pat');
        const idToken = await result.user.getIdToken();

        fetch('https://cabanalabs.com/auth/user/confirm/phone', {
          method: 'POST',
          headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ phoneNumber: number, code, idToken }),
        })
          .then(res => {
            console.log('sent confirm', res.ok, res.status);
            window.top.postMessage('phone_completed', '*');
          })
          .catch(error => {
            console.error(error);
            window.top.postMessage('phone_error', '*');
          });
      })
      .catch(err => {
        alert('Wrong code');
      });
  };

  return (
    <div className={styles.wrapper}>
      {isToVerify ? (
        <div className={styles.container}>
          <PhoneNumberVerification value={otp} onChange={setOtp} />
          <Button id='recaptcha-button' onClick={ValidateOtp}>
            Verify
          </Button>
        </div>
      ) : (
        <div className={styles.container}>
          <Input
            value={number}
            onChange={e => {
              setNumber(e.target.value);
            }}
            placeholder='Phone number'
            type='tel'
          />
          <Button id='recaptcha-button' onClick={sendPhoneNumberOTP}>
            Send OTP
          </Button>
        </div>
      )}
    </div>
  );
};

export default Phone;
